import React from 'react';
import { Slide, Paper, makeStyles, Button, Container } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    page: {
        minHeight: "100vh",
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    innerBox: {
        flexDirection: "column",
    },
    content: {
        display: 'flex',
        flexDirection: "row",
    },
    prevPage: {
        position: "absolute",
        top:0,
        left: "50%",
        backgroundColor: "lightblue",
    },
    nextPage: {
        position: "absolute",
        bottom:0,
        left: "50%",
        backgroundColor: "lightblue",
    },
}));

function PortfolioPage(props) {
    const classes = useStyles();

    return (
        <Slide {...props.slide}>
            <Paper className={classes.page}>
                <Container className={classes.innerBox}>
                    {props.prevPage ? <Button className={classes.prevPage} onClick={props.prevPage}>Prev</Button> : undefined}
                    <Paper className={classes.content}>
                        {props.children}
                    </Paper>
                    {props.nextPage ? <Button className={classes.nextPage} onClick={props.nextPage}>Next</Button> : undefined}
                </Container>
            </Paper>
        </Slide>
    )
}
export default PortfolioPage